import React, { useState } from 'react';
import Button from '../components/ui/Button';
import { signInWithGoogle } from '../services/authService';

const SignUp = ({ onSignUp, onSwitchToSignIn }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    password: '',
    confirmPassword: '',
    role: 'buyer'
  });
  const [error, setError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [showGoogleRoleSelector, setShowGoogleRoleSelector] = useState(false);

  const handleChange = (field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    setError('');
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    // Validate passwords match
    if (formData.password !== formData.confirmPassword) {
      setError('Passwords do not match');
      return;
    }

    setIsLoading(true);

    try {
      await onSignUp(formData);
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleGoogleSignUp = async (selectedRole) => {
    setError('');
    setIsLoading(true);
    setShowGoogleRoleSelector(false);

    try {
      // Google sign-up always uses persistent storage (rememberMe = true)
      await signInWithGoogle(selectedRole || formData.role, true);
      // Auth state listener in App.js will handle the user state
    } catch (err) {
      setError(err.message);
      setIsLoading(false);
    }
  };

  const initiateGoogleSignUp = () => {
    setShowGoogleRoleSelector(true);
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <div className="auth-header">
          <h2> Create Account</h2>
          <p>Join our automated property offer platform</p>
        </div>

        {error && (
          <div className="auth-error">
            <span></span>
            <span>{error}</span>
          </div>
        )}

        <form onSubmit={handleSubmit} className="auth-form">
          <div className="form-group">
            <label>Full Name *</label>
            <input
              type="text"
              className="form-input"
              placeholder="John Doe"
              value={formData.name}
              onChange={(e) => handleChange('name', e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label>Email Address *</label>
            <input
              type="email"
              className="form-input"
              placeholder="john@example.com"
              value={formData.email}
              onChange={(e) => handleChange('email', e.target.value)}
              required
            />
          </div>

          <div className="form-group">
            <label>Phone Number</label>
            <input
              type="tel"
              className="form-input"
              placeholder="(555) 123-4567"
              value={formData.phone}
              onChange={(e) => handleChange('phone', e.target.value)}
            />
          </div>

          <div className="form-group">
            <label>I am a *</label>
            <div className="button-group">
              <Button
                type="button"
                variant="filter"
                active={formData.role === 'buyer'}
                onClick={() => handleChange('role', 'buyer')}
              >
                 Property Buyer
              </Button>
              <Button
                type="button"
                variant="filter"
                active={formData.role === 'owner'}
                onClick={() => handleChange('role', 'owner')}
              >
                 Property Owner
              </Button>
            </div>
          </div>

          <div className="form-group">
            <label>Password *</label>
            <input
              type="password"
              className="form-input"
              placeholder=""
              value={formData.password}
              onChange={(e) => handleChange('password', e.target.value)}
              required
              minLength="6"
            />
            <small className="helper-text">Minimum 6 characters</small>
          </div>

          <div className="form-group">
            <label>Confirm Password *</label>
            <input
              type="password"
              className="form-input"
              placeholder=""
              value={formData.confirmPassword}
              onChange={(e) => handleChange('confirmPassword', e.target.value)}
              required
            />
          </div>

          <div className="form-footer">
            <label className="checkbox-label">
              <input type="checkbox" required />
              <span>I agree to the Terms of Service and Privacy Policy</span>
            </label>
          </div>

          <Button 
            type="submit" 
            variant="primary" 
            className="auth-button"
            disabled={isLoading}
          >
            {isLoading ? ' Creating account...' : ' Create Account'}
          </Button>
        </form>

        <div className="auth-divider">
          <span>or</span>
        </div>

        {showGoogleRoleSelector ? (
          <div className="google-role-selector">
            <p className="role-selector-title">Choose your account type:</p>
            <div className="role-buttons">
              <Button 
                type="button"
                variant="filter"
                onClick={() => handleGoogleSignUp('buyer')}
                disabled={isLoading}
              >
                 Sign up as Buyer
              </Button>
              <Button 
                type="button"
                variant="filter"
                onClick={() => handleGoogleSignUp('owner')}
                disabled={isLoading}
              >
                 Sign up as Owner
              </Button>
            </div>
            <button 
              className="link-button" 
              onClick={() => setShowGoogleRoleSelector(false)}
              style={{ marginTop: '12px' }}
            >
               Back
            </button>
          </div>
        ) : (
          <Button 
            type="button"
            variant="google"
            className="google-button"
            onClick={initiateGoogleSignUp}
            disabled={isLoading}
          >
            <svg className="google-icon" viewBox="0 0 24 24" width="18" height="18">
              <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
              <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
              <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
              <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
            </svg>
            Continue with Google
          </Button>
        )}

        <div className="auth-footer">
          <p>Already have an account?</p>
          <button className="link-button" onClick={onSwitchToSignIn}>
            Sign in instead 
          </button>
        </div>
      </div>
    </div>
  );
};

export default SignUp;

